import { getPluralForm } from '../../../../utils/getPluralForm';

type UploadStatisticsType = {
  filesLeftMessage: string;
  timeLeftMessage: string;
  progressPercent: number;
};

export const getUploadStatistics = (fileList: File[]): UploadStatisticsType => {
  const filePluralForms = ['', '', ''];
  const timePluralForms = ['', '', ''];

  const filesLeft = fileList.length;
  const timeLeft = 2; // 
  const progressPercent = 66; // 

  const filesLeftMessage = ` ${filesLeft} ${getPluralForm(filesLeft, filePluralForms)}`;
  const timeLeftMessage = `  ${timeLeft} ${getPluralForm(
    timeLeft,
    timePluralForms,
  )}`;

  return { filesLeftMessage, timeLeftMessage, progressPercent };
};

export const formatBytes = (bytes: number, decimal: number): string => {
  const marker = 1024;
  const kiloBytes = marker;
  const megaBytes = kiloBytes * marker;
  const gigaBytes = megaBytes * marker;

  if (bytes < kiloBytes) return `${bytes} `;
  if (bytes < megaBytes) return `${(bytes / kiloBytes).toFixed(decimal)} `;
  if (bytes < gigaBytes) return `${(bytes / megaBytes).toFixed(decimal)} `;

  return `${(bytes / gigaBytes).toFixed(decimal)} `;
};

/*

  ""                            -->   ""
  "name"                        -->   ""
  "name.txt"                    -->   "txt"
  ".htpasswd"                   -->   ""
  "name.with.many.dots.ext"     -->   "ext"

*/

export const getExtension = (name: string): string => {
  const index = name.lastIndexOf('.');

  if (name === '' || index < 1) return '';

  return name.slice(index + 1).toLowerCase();
};
